@import url(../../node_modules/continuo/dist/css/continuo.css);
dialog {
  position: absolute;
  left: 0;
  right: 0;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
  height: -moz-fit-content;
  height: -webkit-fit-content;
  height: fit-content;
  margin: auto;
  border: solid;
  padding: 1em;
  background: white;
  color: black;
  display: none; }

dialog[open] {
  display: block; }

dialog + .backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.1); }

._dialog_overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0; }

dialog.fixed {
  position: fixed;
  top: 50%;
  transform: translate(0, -50%); }

.mdl-navigation .mdl-button {
  margin-left: .5em; }

.mdl-layout__header-row {
  padding: 0 25px 0 25px !important; }

.score_header {
  min-height: 45px !important;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1); }
  .score_header .mdl-layout__header-row {
    height: 45px;
    padding: 0 5px 0 5px; }
  .score_header .mdl-layout-title {
    margin: 0 0 0 8px; }

.score {
  padding: 5px;
  overflow: hidden;
  min-height: 500px;
  transition: width 600ms ease-out, height 600ms ease-out; }

.score_collapsed {
  height: 0px;
  overflow: hidden; }

.cnt-container {
  position: relative; }

.cnt-emabox {
  position: absolute !important; }
